/* General section styles */
.section {
    position: relative;
    width: 100%;
    height: auto;
    background: #ffffff;
    top: -15px;
    padding-bottom: 200px;
    /* text-align: center; */
}

.sectionContent {
    padding-bottom: 50px;
    width: 100%;
    max-width: 1000px;
    margin: auto;
}

.sectionTitle {
    width: 800px;
    margin: auto;
    text-align: center;
    font-family: typograph-extrabold;
    font-size: 40px;
    color: #00083c;

    opacity: 0.0;
    transform: translateY(30px);

}

.sectionTitle-animate {
    opacity: 1.0;
    transform: translateY(0px);

    transition: opacity 3s, transform 0.3s;
}

.sectionSubtitleContainer {
    width: 100%;
    text-align: center;
}

.sectionSubtitle-animate {
    overflow: hidden; /* Ensures the content is not revealed until the animation */
    border-right: .15em solid orange; /* The typwriter cursor */
    white-space: nowrap; /*Keeps the content on a single line */
    margin: auto; /* Gives that scrolling effect as the typing happens */
    letter-spacing: .15em; /* Adjust as needed */
    animation: 
      typing 3.5s steps(40, end),
      blink-caret .75s step-end infinite;
}
  
  /* The typing effect */
  @keyframes typing {
    from { max-width: 0 }
    to { max-width: 100% }
  }
  
  /* The typewriter cursor effect */
  @keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: orange; }
  }

.sectionSubtitle {
    /* width: auto; */
    font-size: 24px; 
    font-family: typograph-semibold-italic;
    text-align: center;
    margin-top: 40px;
    margin-bottom: 40px;
    color: #53587a;
    display: inline-block;
    width: auto;
    max-width: 100%;
}

/* About Me section styles */
.aboutMeImageOutline {
    background: linear-gradient(#bac0ff, #fb8686);
    width: 140px;
    height: 140px;
    border-radius: 1000px;
    margin: 40px auto;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center; 

    box-shadow: 3px 3px 20px 10px rgba(0, 0, 0, 0.1);
    z-index: 2;
    position: relative;

    opacity: 0;
    
}

.aboutMeImageOutline-animate {
    width: 340px;
    height: 340px;

    opacity: 1;

    transition: transform 0.5s, opacity 0.5s, width 0.5s, height 0.5s;
}

.aboutMeImage {

    margin: auto;
    border-radius: 1000px;
    background-image: url("../static/profile.jpeg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;

    width: 0px;
    height: 0px;

    
}

.aboutMeImage-animate {
    width: 300px;
    height: 300px;

    transition: width 0.5s, height 0.5s; 
}

.aboutMeTag {
    width: 260px;
    height: 100px;
    border-radius: 30px;
    background-color: rgba(255, 255, 255, 0.8);
    box-shadow: 3px 3px 20px 10px rgba(0, 0, 0, 0.1);
    top: -130px;
    left: -120px;
    position: relative;
    margin: auto;
    box-sizing: border-box;
    padding: 15px;
    z-index: 3;

    opacity: 0;
    transform: translateY(-20px);

}

.aboutMeTag-animate {
    opacity: 1;
    transform: translateY(0px);

    transition: opacity 0.6s, transform 0.6s;
    transition-delay: 0.6s;
}

.nameTag {
    font-family: quando-bold;
    font-weight: 900;
    font-size: 20px;
    padding-top: 5px;
    width: 100%;
}
.ageTag {
    font-family: typograph-light;
    font-weight: 200;
    font-size: 16px;
    padding-top: 5px;
    width: 100%;
}
.locationTag {
    font-family: typograph-light;
    font-size: 14px;
    width: 100%;
    text-align: right;
}

.aboutMeDescription {
    background-color: white;
    width: 75%;
    border-radius: 20px;
    box-shadow: 3px 3px 20px 10px rgba(0, 0, 0, 0.1);
    margin: auto;
    height: auto;
    box-sizing: border-box;
    padding: 20px;
    margin-bottom: 10px;

    font-size: 24px;
    font-weight: 300;
    font-family: typograph-light;
    position: relative;
    top: -100px;

    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    opacity: 0.0;
}

.aboutMeDescription-animate {
    opacity: 1.0;
    transition: opacity 1s linear 0.5s;
}

.aboutMeDescription::before {
	content: '';
	position: absolute;
	width: 0;
	height: 0;
	bottom: 100%;
	left: 1.5em;
	border: .75rem solid transparent;
	border-top: none;

	border-bottom-color: #fff;
	filter: drop-shadow(0 -0.0625rem 0.0625rem rgba(0, 0, 0, .1));
}

