}

.cube {
    width: 100%;
    height: 60px;
    transform-style: preserve-3d;
    transition: 0.1s;
    position: relative;
    display: flex;
    justify-content: center;
}

.face {
    position: absolute;
    width: 350px;
    height: 350px;
    /* opacity: 0.3; */
    /* color: #00083c; */
    color:rgba(0, 0, 0, 0.4);
    opacity: 0.6;
    /* transform-origin: center; */
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    text-align: center;
    box-sizing: border-box;
    padding-top: 20px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    font-size: 30px;
    font-family: typograph-extrabold;
}

.face-front  { 
    transform: rotateY(  0deg) translateZ(30px);
    background: white;
    height: 60px;
}
.face-right  { 
    transform: rotateY( 90deg) translateZ(175px);
    background: rgba(0, 0, 0, 0.4);
    width: 60px;
    height: 60px;
}
.face-back   { 
    transform: rotateY(180deg) translateZ(30px) rotateX(-180deg)rotateY(-180deg); 
    background: white;
    height: 60px;
}
.face-left   { 
    transform: rotateY(-90deg) translateZ(175px);
    background:  rgba(0, 0, 0, 0.4);
    width: 60px;
    height: 60px;
}
.face-top    { 
    transform: rotateX( 90deg) translateZ(30px);
    background: white;
    height: 60px;
}
.face-bottom { 
    transform: rotateX(-90deg) translateZ(30px); ; 
    background: white;
    height: 60px;
}

/* .show-front  { transform: translateZ(-100px) rotateX(   0deg); }
.show-top  { transform: translateZ(-100px) rotateX( -90deg); }
.show-back   { transform: translateZ(-100px) rotateX(-180deg); }
.show-right   { transform: translateZ(-100px) rotateX(  -270deg); } */

.cube-animated {
    transform: translateZ(-100px) rotateX(0deg);
}











.skillContainer {
    width: 100px;
    height: 100px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%) translateY(150%);
}

.skill-large {
    background-color: white;
    height: 86px;
    border-radius: 1000px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    min-width: 86px;

    transform: translateX(0px) translateY(0px);
    opacity: 0;

    box-shadow: 3px 3px 20px 10px rgba(0, 0, 0, 0.1);

    transition: justify-content 0.1s, transform 0.3s, opacity 0.1s;
    transition-delay: 0.4s;
    z-index: -1;
    
}

.icon-skill-large {

    width: 64px;
    height: 64px;
    padding: 0px 20px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;

}

.text-skill-large {
    padding-right: 0px;
    font-size: 16px;
    font-family: typograph-semibold;
    width: 0px;
    opacity: 0;

    transition: width 0.1s, padding 0.1s, opacity 0.1s;
}

.text-skill-large-animated {
    padding-right: 20px;
    font-size: 16px;
    font-family: typograph-semibold;
    width: auto;
    opacity: 1;


    transition: width 0.3s, padding 0.3s, opacity 0.1s;
}

.skill-large-animated {
    justify-content: space-evenly;
    opacity: 1;
    transform: translateX(0px) translateY(0px);
    transition: justify-content 0.1s, transform 0.3s, opacity 0.2s ease-in 0.1s;
}



